import React, { useState } from 'react';
import { getPokemon } from './pokemons';
import { randomLetters } from '../../utils/random';
import './wizard.scss';

const WizardChallenge = () => {

    const [pokemonTeam, setPokemonTeam] = useState([[]]);
    const [letters, setLetters] = useState('');

    const addPokemon = (pokemon, image) => setPokemonTeam([...pokemonTeam, [pokemon, image]]);

    const generateChallenge = () => {
        getPokemon(addPokemon);
        //setPokemonTeam(getPokemon(6));
        setLetters(randomLetters(3));
    }

    const replacePokemonAtIndex = (index) => {
        let team = [...pokemonTeam];
        team[index] = getPokemon();
        setPokemonTeam(team);
    }

    const Pokemon = ({ pokemon, image, index }) => {
        return (
            <div key={"pokemon-" + index} className='d-flex flex-row'>
                <div className='pokemon-name p-2' onClick={() => replacePokemonAtIndex(index)}>
                    {pokemon} <img src={image} alt={pokemon}/>
                </div>
            </div>
        )
    }

    return (
        <div className='wizard card'>
            <h1>Wizard Challenge</h1>
            <p>
                Regler for wizard challenge...
            </p>
            <div className='buttons'>
                <div className='generate-button btn btn-primary m-1' onClick={generateChallenge}> Generate </div>
            </div>
            <h2>Letters: {letters}</h2>
            <div className='column'>
                {pokemonTeam.map((p, i) => (<Pokemon pokemon={p[0]} image={p[1]} index={i} />))}
            </div>
        </div>
    );
}

export default WizardChallenge;